<template>
    <div class="wrap">
      <div class="textCon">
        <p class="textConTitle">.</p>
        <p class="lineheight150"></p>
        <p class="lineheight150">180</p>
      </div>
      <div class="textCon">
        <p class="textConTitle">.</p>
        <div class="textConSecon">
          <p class="textConSmallTitle">1.<span class="line"></span></p>
          <p class="tableIntroduction lineheight150">16030180</p>
          <ComponentTable :tableData="onlineStudyConTable"></ComponentTable>
        </div>
        <div class="textConSecon">
          <p class="textConSmallTitle">2.<span class="line"></span></p>
          <p class="tableIntroduction lineheight150"></p>
          <ComponentTable :tableData="offlineStudyConTable"></ComponentTable>
        </div>
        <div class="textConSecon">
          <p class="textConSmallTitle">3.<span class="line"></span></p>
          <div class="textConSecon2-3">
            <div v-for="(item, index) in textConSecon23Data" :key="index" class="clearfix item">
              <div :class="[{ fl: !(index % 2) }, { fr: index % 2 }, item.imgClass]"></div>
              <div :class="[{ fl: index % 2 }, { fr: !(index % 2) }, 'textConSecon2-3Text']">
                <div class="textConSecon2-3TextF">{{ item.text1 }}<span class="line"></span></div>
                <div class="textConSecon2-3TextT">{{ item.text2 }}</div>
                <div class="textConSecon2-3TextS">{{ item.text3 }}<span class="line"></span></div>
              </div>
            </div>
          </div>
        </div>
        <div class="textConSecon">
          <p class="textConSmallTitle">4.<span class="line"></span></p>
          <div class="textConSecon2-4">
            <div v-for="(item, index) in textConSecon24Data" :key="index" class="clearfix item">
              <div :class="[item.imgClass]"></div>
              <p class="textConSecon4Text lineheight150">{{ item.text }}</p>
            </div>
          </div>
        </div>
      </div>
      <div class="textCon textCon3">
        <p class="textConTitle"> </p>
        <div class="textConSecon">
          <p class="textConSmallTitle">1.<span class="line"></span></p>
          <ul class="textConSecon3-1">
            <li v-for="(item, index) in textConSecon31Data" :key="index" class="item">
              <div :class="[item.imgClass]"></div>
              <div class="text1">{{ item.text1 }}</div>
              <div class="text2">{{ item.text2 }}</div>
            </li>
          </ul>
          <div class="textConSecon3-1Bottom">
            <div class="title">180</div>
            <div class="con">
              <p v-for="(item, index) in textConSecon31Bottom" :key="index">{{ item }}</p>
            </div>
          </div>
        </div>
      </div>
      <div class="textCon bottomContent">
        <p class="textConSmallTitle">2.<span class="line"></span></p>
        <div class="bottomRound">
          <div class="line"></div>
          <div class="item" v-for="(item, index) in bottomRound" :key="index">
            <div :class="['round', `round${index + 1}`]">{{ item.text1 }}</div>
            <p>{{ item.text2 }}</p>
          </div>
        </div>
        <div class="timeAddress">
          <div class="lineheight150">201881</div>
          <div class="lineheight150"></div>
          <div class="lineheight150">400-8650-512</div>
        </div>
        <div class="line2"><div></div></div>
        <div class="line3">ENGLISH TRAINING CAMP</div>
      </div>
    </div>
</template>

<script>
import { onlineStudyConTable, offlineStudyConTable } from './useData'
import ComponentTable from '@/components/cusTable'

export default {
  name: 'onCamp',
  data () {
    return {
      // 2-3 
      textConSecon23Data: [
        {
          text1: '',
          text2: 'Classic culture red wine',
          text3: '',
          imgClass: 'img1'
        },
        {
          text1: '',
          text2: 'Classical culture equestrian',
          text3: '',
          imgClass: 'img2'
        },
        {
          text1: '',
          text2: 'Classical culture golf',
          text3: '',
          imgClass: 'img3'
        }
      ],
      // 2-4 
      textConSecon24Data: [
        {
          imgClass: 'img1',
          text: ''
        },
        {
          imgClass: 'img2',
          text: ' '
        }
      ],
      // 3-1 
      textConSecon31Data: [
        {
          imgClass: 'img3',
          text1: '',
          text2: ''
        },
        {
          imgClass: 'img2',
          text1: '',
          text2: '180'
        },
        {
          imgClass: 'img1',
          text1: '',
          text2: ''
        }
      ],
      // 3-1   180
      textConSecon31Bottom: [
        'A.',
        'B.',
        'C.',
        'D.',
        'E.'
      ],
      // 3-2 
      bottomRound: [
        {
          text1: '',
          text2: 'money'
        },
        {
          text1: '',
          text2: ''
        },
        {
          text1: '',
          text2: ''
        }
      ],
      // 
      onlineStudyConTable: onlineStudyConTable,
      // 
      offlineStudyConTable: offlineStudyConTable
    }
  },
  components: {
    ComponentTable
  }
}
</script>

<style scoped lang="less">
  @import "../base";
  @import "./pbuliceCss";
  .textCon{
    >p{
      margin-bottom: 36px;
      &:last-child{
        margin-bottom: 0;
      }
    }
    &Title{
      font-size: 34px;
      font-weight: bold;
      margin-bottom: 60px !important;
      font-family: "";
    }
    .textConSecon2-3{
      .item{
        width: 100%;
        .fl, .fr{
          width: 50%;
          height: 334px;
        }
        .img1{
          background: url("../images/img1.jpg") center center no-repeat;
          background-size: 100% 100%;
        }
        .img2{
          background: url("../images/img2.jpg") center center no-repeat;
          background-size: 100% 100%;
        }
        .img3{
          background: url("../images/img3.jpg") center center no-repeat;
          background-size: 100% 100%;
        }
      }
      &Text{
        background: @textColor_12;
        padding: 62px 28px 0;
        &F{
          color: @colorf;
          font-size: 34px;
          position: relative;
          .line{
            position: absolute;
            top: -20px;
            left: 0;
            width: 36px;
            height: 7px;
            background: @smallTitleColor;
          }
        }
        &T{
          color: @smallTitleColor;
          font-size: 20px;
          margin-bottom: 50px;
        }
        &S{
          color: @colorf;
          font-size: 28px;
          position: relative;
          .line{
            position: absolute;
            top: -30px;
            left: 0;
            width: 25px;
            height: 3px;
            background: @colorf;
          }
        }
      }
    }
    .textConSecon2-4{
      .item{
        margin-bottom: 40px;
        &:last-child{
          margin-bottom: 0;
        }
      }
      .textConSecon4Text{
        font-size: 28px;
        color: @color3;
        margin-top: 20px;
      }
      .img1{
        width: 100%;
        height: 300px;
        background: url("../images/img4.jpg") center center no-repeat;
        background-size: 100% 100%;
      }
      .img2{
        width: 100%;
        height: 300px;
        background: url("../images/img5.jpg") center center no-repeat;
        background-size: 100% 100%;
      }
    }
    &.textCon3{
      padding-left: 20px;
      padding-right: 20px;
      >*{
        padding-left: 20px;
        padding-right: 20px;
      }
      .textConSecon{
        padding-left: 0;
        padding-right: 0;
      }
    }
    .textConSecon3-1{
      display: flex;
      justify-content: space-between;
      .item{
        width: 220px;
        height: 320px;
        box-shadow: 0 0 10px rgba(0, 0, 0, .5);
        padding-top: 110px;
        padding-left: 20px;
        padding-right: 20px;
        position: relative;
        margin-bottom: 44px;
        .text1{
          margin-bottom: 30px;
          font-size: 30px;
          color: @textColor_12;
          font-weight: bold;
          text-align: center;
        }
        .text2{
          font-size: 24px;
          color: @color3;
        }
        .img1, .img2, .img3{
          position: absolute;
          top: 24px;
          left: 50%;
          transform: translateX(-50%);
        }
        .img1{
          width: 76px;
          height: 60px;
          margin: 0 auto;
          background: url("../images/icon_1.png") center center no-repeat;
          background-size: 100% 100%;
        }
        .img2{
          width: 62px;
          height: 67px;
          margin: 0 auto;
          background: url("../images/icon_2.png") center center no-repeat;
          background-size: 100% 100%;
        }
        .img3{
          width: 76px;
          height: 60px;
          margin: 0 auto;
          background: url("../images/icon_3.png") center center no-repeat;
          background-size: 100% 100%;
        }
      }
    }
    .textConSecon3-1Bottom{
      padding-left: 20px !important;
      padding-right: 20px !important;
      .title{
        font-size: 30px;
        color: @textColor_1b;
        font-weight: bold;
        margin-bottom: 24px;
      }
      .con{
        font-size: 28px;
        color: @color3;
        p{
          margin-bottom: 15px;
          &:last-child{
            margin-bottom: 0;
          }
        }
      }
    }
    &.bottomContent{
      background: url("../images/bottom.png") top center no-repeat;
      background-size: 100% 725px;
      padding: 0;
      margin-bottom: 0;
      color: @colorf;
      padding-bottom: 30px;
      .textConSmallTitle{
        margin-bottom: 74px;
      }
      .bottomRound{
        display: flex;
        justify-content: space-around;
        padding: 0 @normalPaddingLR;
        margin-bottom: 52px;
        position: relative;
        .line{
          position: absolute;
          width: 670px;
          height: 2px;
          background: #eee;
          bottom: -20px;
          opacity: .7;
        }
        .item{
          width: 196px;
          display: flex;
          flex-direction: column;
          .round{
            width: 162px;
            height: 162px;
            margin: 0 auto;
            border-radius: 50%;
            background: #586DA7;
            box-shadow: 0 0 20px rgba(255, 255, 255, .5);
            color: @colorf;
            display: flex;
            align-items: center;
            justify-content: center;
            margin-bottom: 26px;
            &.round2{
              background: #6476A7;
              text-align: center;
            }
            &.round3{
              background: #7786AE;
            }
          }
        }
      }
      .timeAddress{
        padding: 0 40px;
        margin-bottom: 32px;
      }
      .line2{
        div{
          width: 50px;
          height: 4px;
          background: @colorf;
        }
        padding-left: 40px;
        margin-bottom: 12px;
      }
      .line3{
        font-size: 16px;
        text-indent: 40px;
      }
    }
  }
</style>



