<template>
  <div class="textCont">
    <div class="content">
      <div class="culHeader">
        <span></span>
        <img style="display: none;" @click="wxRegCallback()" src="../images/share1_03.png" alt="">
      </div>
      <div class="culText">
        <p class="pTime">2018-07-23</p>
        <div class="pText">
          <p></p>
          <p>
            
            
            </p>
        </div>
        <img src="../images/cul_1.png" alt="">
        <p></p>
      </div>
      <div class="lookOut"></div>
    </div>
  </div>
</template>
<script>
import { mapGetters } from 'vuex'
import { returnBtn } from './returnBtn'
export default {
  name: 'detaila',
  beforeRouteEnter (to, from, next) {
    // 
    returnBtn(to, from, next)
  },
  data () {
    return {
      jsbData: {
        title: {
          titName: this.$route.meta.title,
          titleImgUrl: 'https://static.hulian120.com/web/eglishtrain/english_train_w.png',
          titleImgColor: '161616',
          titleImgAlpha: '1',
          titleCntColor: ''
        },
        return: {
          ui_elm: 'return_btn',
          visible: 'true',
          position: '1',
          goback_add: 'direct'
        },
        btn: [
          {
            ui_elm: 'return_btn',
            visible: 'true',
            position: '1'
          },
          {
            ui_elm: 'close_btn',
            visible: 'false',
            position: '2'
          }
        ]
      }
    }
  },
  computed: {
    ...mapGetters(['getUserInfo'])
  }
}
</script>
<style scoped lang="less">
  .content{
    padding: 40px;
  }
  .culHeader{
    border-bottom: 1px solid #e5e5e5;
    padding: 0 0 20px 0;
    span{
      font-size: 40px;
      font-weight: bold;
    }
    img{
      width: 36px;
      height: 36px;
      float: right;
    }
  }
  .culText{
    margin: 20px 0 90px 0;
    font-size: 32px;
    .pTime{
      font-size: 28px;
      color: #999999;
    }
    .pText{
      margin-top: 50px;
    }
    img{
      width: 100%;
      height: 300px;
      margin: 29px 0 29px 0;
    }
    p{
      text-indent: 65px;
    }
  }
  .lookOut{
    font-size: 28px;
    color: #12224f;
  }
</style>



