<template>
  <div class="textCont">
    <div class="content">
      <div class="culHeader">
        <span class="titleCul">-</span>
        <img style="display: none;" @click="share()" src="../images/share1_03.png" alt="">
      </div>
      <div class="culText">
        <p class="pTime">2018-07-23</p>
        <div class="pText">
          <p>1Tina </p>
          <img src="../images/grow_1_1.jpg" alt="">
          <div class="depict">Tina10
            </div>
          <img src="../images/grow_2_2.jpg" alt="">
          <div class="depict">
            </div>
        </div>
        <div class="pText">
          <p>2Anna  </p>
          <p class="depict">Anna1
            </p>
          <img src="../images/grow_3_3.jpg" alt="">
          <div class="depict">Anna
            medicalOR</div>
          <img src="../images/grow_4_4.jpg" alt="">
        </div>
        <div class="pText">
          <p>3Samantha </p>
          <img src="../images/grow_5_5.jpg" alt="">
          <div class="depict">Samantha25
            </div>
          <img src="../images/grow_6_6.jpg" alt="">
          <div class="depict">Samantha
            </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { mapGetters } from 'vuex'
import $ from 'jquery'
import { jsbGoShare } from '@/config/jsb/hybird'
import wx from 'weixin-js-sdk'
import { returnBtn } from './returnBtn'
export default {
  name: 'detailaCz',
  beforeRouteEnter (to, from, next) {
    // 
    returnBtn(to, from, next)
  },
  data () {
    return {
      jsbData: {
        title: {
          titName: this.$route.meta.title,
          titleImgUrl: 'https://static.hulian120.com/web/eglishtrain/english_train_w.png',
          titleImgColor: '161616',
          titleImgAlpha: '1',
          titleCntColor: ''
        },
        return: {
          ui_elm: 'return_btn',
          visible: 'true',
          position: '1',
          goback_add: 'direct'
        },
        btn: [
          {
            ui_elm: 'return_btn',
            visible: 'true',
            position: '1'
          },
          {
            ui_elm: 'close_btn',
            visible: 'false',
            position: '2'
          }
        ]
      },
      scrollWatch: true
    }
  },
  mounted () {
    $(window).scrollTop(0)
  },
  methods: {
    share: function () {
      isweixin()
    }
  },
  computed: {
    ...mapGetters(['getUserInfo'])
  }
}
function isweixin () {
  const ua = window.navigator.userAgent.toLowerCase()
  if (ua.match(/MicroMessenger/i) == 'micromessenger') {
    shareOk()
  } else {
    checkAgent()
  }
}
function checkAgent () {
  let u = navigator.userAgent
  let isAndroid = u.indexOf('Android') > -1 || u.indexOf('Linux') > -1
  let isiOS = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/)
  if (isAndroid || isiOS) {
    let title = $('.titleCul').text()
    let shareContent = $('.shareContent').text()
    let shareImg = 'https://sivt.hulian120.com/bl.jpg'
    let option = window.location.href + ',' + title + ',' + shareContent + ',' + shareImg
    jsbGoShare(option)
  } else {
    console.log(2)
  }
}
function shareOk () {
  $.ajax({
    noToken: true,
    type: 'get',
    url: 'https://b.hulian120.com/wechat/portal/wxJsApiSignature',
    data: {
      url: location.href.split('#')[0]
    },
    success: function (data) {
      // 
      console.log(data)
      wx.config({
        debug: true,
        appId: data.respResult.appId,
        timestamp: data.respResult.timestamp,
        nonceStr: data.respResult.nonceStr,
        signature: data.respResult.signature,
        jsApiList: [
          'onMenuShareAppMessage', // 
          'onMenuShareTimeline' // 
        ] // JSJS2
      })
      wx.ready(function () {
        //  JS 
        // 
        wx.onMenuShareAppMessage({
          title: '111111111111',
          desc: '22222222222',
          link: 'https://sivt.hulian120.com',
          imgUrl: 'https://sivt.hulian120.com/bl.jpg'
        })
        // 
        wx.onMenuShareTimeline({
          title: '111111111111',
          desc: '22222222222',
          link: 'https://sivt.hulian120.com',
          imgUrl: 'https://sivt.hulian120.com/bl.jpg'
        })
      })
    },
    error: function (error) {
      alert('4' + error)
    }
  })
}
</script>
<style scoped lang="less">
  .content{
    padding: 40px;
  }
  .culHeader{
    border-bottom: 1px solid #e5e5e5;
    padding: 0 0 20px 0;
    span{
      font-size: 40px;
      font-weight: bold;
    }
    img{
      width: 36px;
      height: 36px;
      float: right;
    }
  }
  .culText{
    margin: 20px 0 90px 0;
    font-size: 32px;
    .pTime{
      font-size: 28px;
      color: #999999;
    }
    .pText{
      margin-top: 50px;
    }
    img{
      width: 100%;
      margin: 10px 0 10px 0;
    }
    p{
      margin-top: 30px;
      text-indent: 65px;
    }
    .depict{
      font-size: 32px;
    }
  }
  .lookOut{
    font-size: 28px;
    color: #12224f;
  }
</style>



